.maps {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 10%;
  overflow: hidden;
}

.maps h1 {
  /* Add any specific styles for the h1 if needed */
}

.maps p {
  font-size: 1.5em;
  padding: 6%;
}

.GridMaps {
  display: grid;
  width: 100%;
}

.GridMaps iframe {
  width: 100%;
  height: 600px;
  overflow: hidden;
  border: none !important;
}